import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import "./TopBanner.css";

const TopBanner = () => {
  return (
    <section className='top_banner'>
      <Container className='h-100'>
        <Row md='12' className='text-center h-100'>
          <h1>
            Welcome to <span>EduVibe</span>
          </h1>

          <Col className='text-center d-flex align-items-center justify-content-start'>
            <div className='top_banner_content'>
              {/* <p>Learn coding with interactive tutorial</p> */}
            </div>
          </Col>
          <Link className='top_banner_content ' to='/course'>
            <button>View our Courses</button>
          </Link>
        </Row>
      </Container>
    </section>
  );
};

export default TopBanner;
